<template>
  <div class="config-bar-box">
    <el-row>
      <el-tabs v-model="configTab" type="card" :stretch="true">
        <el-tab-pane label="" name="basic" v-if="cptDataFormShow">
          <el-form
            label-width="90px"
            label-position="left"
            size="mini"
          >
            <el-form-item label="">
              <el-input-number
                :min="20"
                :max="2000"
                v-model="currentPosition.cptWidth"
                size="mini"
                @change="changeConfig"
              />
            </el-form-item>
            <el-form-item label="">
              <el-input-number
                :min="20"
                :max="1500"
                v-model="currentPosition.cptHeight"
                size="mini"
                @change="changeConfig"
              />
            </el-form-item>
            <el-form-item label="X">
              <el-input-number
                :min="-500"
                :max="2500"
                v-model="currentPosition.cptX"
                size="mini"
                @change="changeConfig"
              />
            </el-form-item>
            <el-form-item label="Y">
              <el-input-number
                :min="-500"
                v-model="currentPosition.cptY"
                size="mini"
                @change="changeConfig"
              />
            </el-form-item>
            <el-form-item label="Z">
              <el-input-number
                :min="1"
                :max="1800"
                v-model="currentPosition.cptZ"
                size="mini"
                @change="changeConfig"
              />
            </el-form-item>
          </el-form>
        </el-tab-pane>
        <el-tab-pane label="" name="custom" v-if="cptDataFormShow">
          <div class="customForm" v-if="currentCpt && currentCpt.option">
            <comment
              :is="currentCpt.cptName + '-option'"
              :attribute="currentCpt.option.attribute"
            ></comment>
          </div>
        </el-tab-pane>
        <!-- option.jscptDataForm -->
        <el-tab-pane label="" name="data" v-if="cptDataFormShow">
          <div class="customForm">
            <el-form size="mini" label-position="top">
              <el-form-item label="">
                <el-radio-group
                  v-model="currentCpt.option.cptDataForm.dataSource"
                >
                  <el-radio :label="1"></el-radio>
                  <el-radio :label="2"></el-radio>
                  <el-radio :label="3">sql</el-radio>
                </el-radio-group>
              </el-form-item>
              <el-form-item label="">
                <el-switch
                  v-model="dataPollEnable"
                  active-text=""
                  inactive-text=""
                />
              </el-form-item>
              <el-form-item label="(s)" v-show="dataPollEnable">
                <el-input-number
                  v-model="currentCpt.option.cptDataForm.pollTime"
                  :min="0"
                  :max="100"
                  label=""
                />
              </el-form-item>
              <el-form-item
                :label="
                  dataLabels[currentCpt.option.cptDataForm.dataSource - 1]
                "
              >
                <el-input
                  type="textarea"
                  :rows="5"
                  v-model="currentCpt.option.cptDataForm.dataText"
                />
              </el-form-item>
              <el-form-item>
                <el-button
                  type="primary"
                  style="width: 100%"
                  @click="refreshCptData"
                  ></el-button
                >
              </el-form-item>
            </el-form>
          </div>
        </el-tab-pane>
      </el-tabs>
    </el-row>

    <!--  -->
    <!-- <screen-setting v-else></screen-setting> -->
    <!-- <no-data v-else></no-data> -->
  </div>
</template>

<script>
import NoData from "@/components/NoData/NoData.vue";
import ScreenSetting from "./ScreenSetting.vue";
export default {
  name: "configBar",
  props: {
    currentCpt: Object,
  },
  data() {
    return {
      cptDataFormShow: false,
      configTab: "custom",
      currentPosition: {
        cptWidth: 30,
        cptHeight: 30,
        cptX: 0,
        cptY: 0,
        cptZ: 0,
      },
      dataLabels: ["", "", "sql"],
      configBarShow: false,
    };
  },
  components: {
    NoData,
    ScreenSetting,
  },
  watch: {
    currentCpt(newVal) {
      this.cptDataFormShow = false;
      console.log("newVal", newVal);
      if (!newVal) {
        //
        this.configBarShow = false;
      } else {
        if (this.currentCpt.option.cptDataForm) {
          this.cptDataFormShow = true;
        } else {
          this.configTab = "custom"; //tabbug
        }
      }
    },
  },
  computed: {
    dataPollEnable: {
      get() {
        return !!(
          this.currentCpt.option.cptDataForm &&
          this.currentCpt.option.cptDataForm.pollTime &&
          this.currentCpt.option.cptDataForm.pollTime !== 0
        );
      },
      set(newValue) {
        if (newValue) {
          this.currentCpt.option.cptDataForm.pollTime = 8;
        } else {
          this.currentCpt.option.cptDataForm.pollTime = 0;
          this.refreshCptData(); //
        }
        return newValue;
      },
    },
  },
  methods: {
    // (index)refreshCptData
    // refreshCptData
    // refreshCptDataoption.cptDataForm
    refreshCptData() {
      this.$emit("refreshCptData");
    },
    showConfigBar() {
      this.configBarShow = true;
    },
    updateData(currentPosition) {
      this.currentPosition = currentPosition;
      // console.log("",this.currentPosition.cptX,this.currentPosition.cptY)
    },
    changeConfig() {
      console.log("", this.cptDataFormShow);
      if (this.cptDataFormShow) {
        this.$emit("change", this.currentPosition);
      }
    },
  },
};
</script>

<style lang="less" scoped>
.config-bar-box {
  color: #fff;
  /deep/ .el-tabs__content {
    padding: 20px 20px;
    box-sizing: border-box;
  }
}
/deep/ .el-tabs__header {
  position: sticky;
  top: 35px;
  background-color: #fff;
  z-index: 999;
}
/deep/ .el-form-item__label {
  color: #fff;
}
.customForm {
  padding: 0 6px 0 4px;
  overflow: auto;
}
.configTs-enter-active,
.configTs-leave-active {
  transition: all 0.3s;
}
.configTs-enter,
.configTs-leave-to {
  opacity: 0;
  transform: scale(0.3);
  transform-origin: right bottom;
}
</style>
